@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

.btn {
  color: #ececec;
  border: 2px solid white;
  border-radius: 5px;
  font-family: "Roboto Mono", monospace;
  text-transform: uppercase;
}

.btn:hover {
  color: #0e0e0e;
  background: #f8f8f8;
  box-shadow: 0px 4.42184px 107.23px rgba(102, 102, 102, 0.51);
  border: 2px solid white;
  border-radius: 5px;
}
.shadow{
  box-shadow: 0px 4.42184px 107.23px rgba(102, 102, 102, 0.51);
}
.gradient,
.active {
  background: transparent;
  border: 1px solid white;
}

.active {
  color: #fff;
  padding: 1rem;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-white font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#42A6E3] to-[#0937db] font-bold font-primary hover:from-[#1640D6] hover:to-[#1640D6];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
